import React from 'react';
import { NavLink } from 'react-router-dom';

import './styles/ModeChooser.css';

const ModeChooser = ({ active, modeSelectedHandler }) => {
  return (
    <div className="ModeChooser">
      <button
        to="/url"
        className={`choose-btn ${active === 1 ? 'active' : null}`}
        onClick={() => modeSelectedHandler(1)}
      >
        URL
      </button>
      <button
        to="/upload"
        className={`choose-btn ${active === 2 ? 'active' : null}`}
        onClick={() => modeSelectedHandler(2)}
      >
        File Upload
      </button>
    </div>
  );
};

export default ModeChooser;
