import React, { useState, useEffect } from 'react';
import './App.css';
import { Route, Switch, withRouter } from 'react-router-dom';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

//Containers import
import Header from './containers/Header/Header';
import Main from './containers/Main/Main';
import SignIn from './containers/SignIn/SignIn.jsx';
import SignUp from './containers/SignUp/SignUp';
import History from './containers/History/History';

//UI import
import ErrorModal from './components/UI/ErrorModal/ErrorModal';
import SuccessModal from './components/UI/SuccessModal/SuccessModal';

//Context import
import AuthContext from './context/authContext';

//Services import
import signUp from './service/signUp';
import signIn from './service/signIn';
import authContext from './context/authContext';

function App(props) {
  const [newUser, setNewUser] = useState({
    name: '',
    username: '',
    email: '',
    password: ''
  });
  const [loginData, setLoginData] = useState({
    username: '',
    password: ''
  });
  const [serverVal, setServerVal] = useState({
    email: false,
    username: false
  });
  const [loggedUser, setLoggedUser] = useState(null);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);
  const [modalAnimation, setModalAnimation] = useState('');
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    return window.localStorage.getItem('loggedFaceRecogAppUser')
      ? (() => {
          const user = JSON.parse(
            window.localStorage.getItem('loggedFaceRecogAppUser')
          );
          setLoggedUser({
            name: user.name,
            username: user.username,
            token: user.token,
            id: user.id
          });
        })()
      : null;
  }, []);

  const inputChangeHandler = e => {
    switch (e.target.parentNode.parentNode.id) {
      case 'signUp':
        setNewUser({
          ...newUser,
          [e.target.name]: e.target.value
        });
        break;
      case 'signIn':
        setLoginData({
          ...loginData,
          [e.target.name]: e.target.value
        });
        break;
    }
  };

  const signUpHandler = async () => {
    setServerVal({
      email: false,
      username: false
    });
    try {
      setLoading(true);
      await signUp(newUser);
      setNewUser({
        name: '',
        username: '',
        email: '',
        password: ''
      });
      props.history.push('/');
      setLoading(false);
      setModalHandler(
        'SUCCESS',
        'Registration successfull',
        'Sign in to be able to use face detection'
      );
    } catch (err) {
      console.log('kjlgkjg', err.response.data.err);
      if (err.response.data.err.errors.email)
        setServerVal({
          ...serverVal,
          email: true
        });

      if (err.response.data.err.errors.username)
        setServerVal({
          ...serverVal,
          username: true
        });

      setModalHandler(
        'ERROR',
        'Registration error',
        'Error while data validation'
      );

      setLoading(false);
    }
  };

  const signInHandler = async e => {
    e.preventDefault();
    try {
      const user = await signIn(loginData);
      console.log(user);
      setLoggedUser(user);
      setLoginData({ username: '', password: '' });
      window.localStorage.setItem(
        'loggedFaceRecogAppUser',
        JSON.stringify(user)
      );
      props.history.push('/');
      setModalHandler('SUCCESS', 'You are now logged in', '');
    } catch (err) {
      setModalHandler(
        'ERROR',
        'Wrong Login Data',
        'Either your username or your password were wrong, try another ones'
      );
      setLoginData({ username: '', password: '' });
    }
  };

  const signOutHandler = () => {
    setLoggedUser(null);
    localStorage.removeItem('loggedFaceRecogAppUser');
  };

  const setModalHandler = (type, header, text) => {
    switch (type) {
      case 'SUCCESS':
        setSuccess(null);
        setSuccess({
          header,
          text
        });
        break;
      case 'ERROR':
        setError(null);
        setError({
          header,
          text
        });
        break;
    }
    setModalAnimation('fadeIn .7s forwards ease');
    setTimeout(() => {
      setModalAnimation('fadeOut 0.7s forwards ease');
      setTimeout(() => {
        setError(null);
        setSuccess(null);
      }, 700);
    }, 3000);
  };

  return (
    <AuthContext.Provider
      value={{
        loggedUser: loggedUser,
        signInHandler: signInHandler,
        signUpHandler: signUpHandler,
        signOutHandler: signOutHandler,
        loadingState: {
          loading,
          setLoading
        },
        serverVal: {
          email: serverVal.email,
          username: serverVal.username
        }
      }}
    >
      <div className="App">
        {error ? <ErrorModal error={error} animation={modalAnimation} /> : null}
        {success ? (
          <SuccessModal success={success} animation={modalAnimation} />
        ) : null}
        <Header />
        <TransitionGroup>
          <CSSTransition
            key={props.location.key}
            timeout={{ enter: 300, exit: 300 }}
            classNames="fade"
          >
            <Switch location={props.location}>
              <Route
                path="/"
                exact
                render={() => (
                  <Main error={error} setModalHandler={setModalHandler} />
                )}
              />
              <Route
                path="/sign-in"
                render={() => (
                  <SignIn
                    loginData={loginData}
                    inputChangeHandler={inputChangeHandler}
                  />
                )}
              />
              <Route
                path="/sign-up"
                render={() => (
                  <SignUp
                    newUser={newUser}
                    inputChangeHandler={inputChangeHandler}
                  />
                )}
              />
              <Route
                path="/history"
                render={() => {
                  return <History />;
                }}
              />
            </Switch>
          </CSSTransition>
        </TransitionGroup>
      </div>
    </AuthContext.Provider>
  );
}

export default withRouter(App);
