import React, { useContext } from 'react';
import AuthContext from '../../context/authContext';

import LoadingSpinner from '../../components/UI/LoadingSpinner/LoadingSpinner';

import classes from './styles/SignIn.module.css';

function SignIn({ loginData, inputChangeHandler }) {
  const authContext = useContext(AuthContext);

  return (
    <form method="submit" className={classes.SignIn} id="signIn">
      {authContext.loadingState.loading ? (
        <LoadingSpinner />
      ) : (
        <>
          <h2>Sign In</h2>
          <div className={classes.form__field}>
            <label htmlFor="username">Username</label>
            <input
              type="username"
              value={loginData.username}
              name="username"
              placeholder="example: jdoe123"
              onChange={e => inputChangeHandler(e)}
            />
          </div>
          <div className={classes.form__field}>
            <label htmlFor="password">Password</label>
            <input
              type="password"
              value={loginData.password}
              name="password"
              placeholder="example: qwerty12345"
              onChange={e => inputChangeHandler(e)}
            />
          </div>
          <button type="submit" onClick={e => authContext.signInHandler(e)}>
            Sign In
          </button>
        </>
      )}
    </form>
  );
}

export default SignIn;
