import React, { useContext, useRef } from 'react';
import AuthContext from '../../context/authContext';
import { useForm } from 'react-hook-form';

import LoadingSpinner from '../../components/UI/LoadingSpinner/LoadingSpinner';

import classes from './styles/SignUp.module.css';

function SignUp({ newUser, inputChangeHandler }) {
  const { name, username, email, password } = newUser;
  const authContext = useContext(AuthContext);
  const { register, handleSubmit, errors, watch } = useForm();

  const passwordRef = useRef({});
  passwordRef.current = watch('password', '');

  return (
    <form
      onSubmit={e => e.preventDefault()}
      className={classes.SignUp}
      id="signUp"
    >
      {authContext.loadingState.loading ? (
        <LoadingSpinner />
      ) : (
        <>
          <div className={classes['form-field']}>
            <label htmlFor="name">Name</label>
            <input
              name="name"
              ref={register({
                required: 'You must put in your name',
                maxLength: 20
              })}
              defaultValue={name}
              onChange={e => inputChangeHandler(e)}
              placeholder="example: John Doe"
              autoFocus
            />
            {errors.name && <p>{errors.name.message}</p>}
          </div>
          <div className={classes['form-field']}>
            <label htmlFor="email">Email</label>
            <input
              name="email"
              ref={register({
                required: 'You must provide an existing email',
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                  message: 'invalid email address'
                }
              })}
              defaultValue={email}
              onChange={e => inputChangeHandler(e)}
              placeholder="example: jdoe1990@example.com"
            />
            {(errors.email && <p>{errors.email.message}</p>) ||
              (authContext.serverVal.email && (
                <p>This email is already signed to other account</p>
              ))}
          </div>
          <div className={classes['form-field']}>
            <label htmlFor="username">Username</label>
            <input
              name="username"
              ref={register({
                required: 'You must specify a unique username'
              })}
              defaultValue={username}
              onChange={e => inputChangeHandler(e)}
              placeholder="example: jdoe123"
            />
            {(errors.username && <p>{errors.username.message}</p>) ||
              (authContext.serverVal.username && (
                <p>This username is already used</p>
              ))}
          </div>
          <div className={classes['form-field']}>
            <label htmlFor="password">Password</label>
            <input
              name="password"
              type="password"
              ref={register({
                required: 'You must specify a password',
                minLength: {
                  value: 8,
                  message: 'Password must have at least 8 characters'
                }
              })}
              defaultValue={password}
              onChange={e => inputChangeHandler(e)}
              placeholder="example: qwerty12345"
            />
            {errors.password && <p>{errors.password.message}</p>}
          </div>
          <div className={classes['form-field']}>
            <label>Repeat password</label>
            <input
              name="password_repeat"
              type="password"
              ref={register({
                validate: value =>
                  value === passwordRef.current || 'The passwords do not match'
              })}
            />
            {errors.password_repeat && <p>{errors.password_repeat.message}</p>}
          </div>
          <button
            type="submit"
            onClick={handleSubmit(authContext.signUpHandler)}
          >
            Sign Up
          </button>
        </>
      )}
    </form>
  );
}

export default SignUp;

