import React, { useContext } from 'react';
import { NavLink } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import MediaQuery from 'react-responsive';

import AuthContext from '../../context/authContext';

import Backdrop from '../UI/Backdrop/Backdrop';

import './styles/Nav.css';

function Nav({ burgerClickedHandler, isClicked }) {
  const authContext = useContext(AuthContext);

  const isMobile = useMediaQuery({ query: '(max-width: 1120px)' });

  return (
    <>
      {isClicked && isMobile && (
        <Backdrop burgerClickedHandler={burgerClickedHandler} />
      )}
      <nav className={`Nav ${isClicked ? 'active' : ''}`}>
        <NavLink
          to="/"
          className="nav__item"
          onClick={() => burgerClickedHandler()}
        >
          Home
        </NavLink>
        {!authContext.loggedUser ? (
          <>
            <NavLink
              to="/sign-in"
              className="nav__item"
              onClick={() => burgerClickedHandler()}
            >
              Sign In
            </NavLink>
            <NavLink
              to="/sign-up"
              className="nav__item"
              onClick={() => burgerClickedHandler()}
            >
              Sign Up
            </NavLink>
          </>
        ) : (
          <>
            <NavLink to="/history" className="nav__item">
              History
            </NavLink>
            <NavLink
              to="/"
              className="nav__item"
              onClick={() => authContext.signOutHandler()}
            >
              Sign Out
            </NavLink>
          </>
        )}

        <div className="exit" onClick={() => burgerClickedHandler()}></div>
      </nav>
    </>
  );
}

export default Nav;
