import React, { useRef } from 'react';

import classes from './styles/FileInput.module.css';

function FileInput({ fileChangeHandler, filePredictHandler }) {
  const fileInputRef = useRef();

  const onChooseFileHandler = () => {
    fileInputRef.current.click();
  };

  return (
    <div className={classes.FileInput}>
      <input
        type="file"
        ref={fileInputRef}
        accept=".jpg, .jpeg, .png"
        style={{ display: 'none' }}
        onChange={e => fileChangeHandler(e)}
      />
      <button type="submit" onClick={e => onChooseFileHandler(e)}>
        Choose File
      </button>
    </div>
  );
}

export default FileInput;
