import React, { useState, useContext, useEffect } from 'react';
import { Route, Switch } from 'react-router-dom';
import classes from './styles/Main.module.css';

import URLInput from '../../components/InputField/InputField';
import PhotoField from '../../components/PhotoField/PhotoField';
import ModeChooser from '../../components/ModeChooser/ModeChooser';
import FileInput from '../../components/FileInput/FileInput';

import AuthContext from '../../context/authContext';

import predictServices from '../../service/predict';

function Main({ setModalHandler }) {
  const authContext = useContext(AuthContext);

  const [url, setUrl] = useState('');
  const [file, setFile] = useState(null);
  const [photo, setPhoto] = useState(null);
  const [faceRect, setFaceRect] = useState([]);
  const [active, setActive] = useState(1);

  useEffect(() => {
    if (!file) return;
    else filePredictHandler(file);
  }, [file]);

  const inputChangeHandler = ({ target }) => {
    setUrl(target.value);
  };

  const urlMatch = url => {
    const expression = /[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)?/gi;
    const regex = new RegExp(expression);
    return url.match(regex);
  };

  const modeSelectedHandler = num => {
    if (active === num) return;
    else setActive(num);
  };

  const fileChangeHandler = e => {
    setFile(e.target.files[0]);
  };

  const filePredictHandler = e => {
    setFaceRect([]);
    setPhoto(null);
    authContext.loadingState.setLoading(true);

    const formData = new FormData();
    formData.append('img', file);
    
    formData.append('user', JSON.stringify(authContext.loggedUser))
    predictServices
      .predictFile(formData)
      .then(res => {
        setPhoto(res.outputs[0].input.data.image.url);
        setFaceRect([
          ...res.outputs[0].data.regions.map(
            el => el['region_info']['bounding_box']
          )
        ]);
        authContext.loadingState.setLoading(false);
      })
      .catch(err => {
        setModalHandler(
          'ERROR',
          'Nothing found',
          'No face detected, please try another photo'
        );
        authContext.loadingState.setLoading(false);
      });
  };

  const URLPredictHandler = e => {
    setFaceRect([]);
    setPhoto(null);
    authContext.loadingState.setLoading(true);

    urlMatch(url)
      ? predictServices
          .predictUrl({url, user: authContext.loggedUser})
          .then(res => {
            setPhoto(res.outputs[0].input.data.image.url);
            setFaceRect([
              ...res.outputs[0].data.regions.map(
                el => el['region_info']['bounding_box']
              )
            ]);
            authContext.loadingState.setLoading(false);
            setUrl('');
          })
          .catch(err => {
            setModalHandler(
              'ERROR',
              'Nothing found',
              'No face detected, please try another photo'
            );
            authContext.loadingState.setLoading(false);
            setUrl('');
          })
      : (() => {
          setModalHandler(
            'ERROR',
            'Input Error',
            'Wrong input type, make sure your url is valid'
          );
          authContext.loadingState.setLoading(false);
          setUrl('');
        })();
  };

  return (
    <div className={classes.Main}>
      {authContext.loggedUser ? (
        <>
          <ModeChooser
            active={active}
            modeSelectedHandler={modeSelectedHandler}
          />
          {active === 1 ? (
            <URLInput
              url={url}
              inputChangeHandler={inputChangeHandler}
              URLPredictHandler={URLPredictHandler}
            />
          ) : (
            <FileInput
              fileChangeHandler={fileChangeHandler}
              filePredictHandler={filePredictHandler}
            />
          )}

          <PhotoField
            photo={photo}
            faceRect={faceRect}
            loading={authContext.loadingState.loading}
          />
        </>
      ) : (
        <div className={classes.mustLogIn}>
          <p>You must be logged in to be able to use face detection</p>
        </div>
      )}
    </div>
  );
}

export default Main;
